#container {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
}

#container h1 {
  letter-spacing: 2px;
  margin-bottom: 20px;
  font-family: Raleway, sans-serif;
  font-size: 2em;
  font-weight: bold;
}

