  border-radius: 10px;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  padding: 20px;
  list-style-type: none;
  display: flex;
  box-shadow: 0 4px 8px #0000001a, 0 0 20px #ffffff80;
}

li {
  border: 2px solid #e7e7e7;
  flex-direction: column;
  padding: 10px;
  display: flex;
}

